export default {
    partial_patch_listTakenSlots ({commit, rootState}, [vm, select, payload]) {
        const clinicID = select.id
        const workspaces = rootState.auth.actorContext.workspaces
        payload.name = workspaces[clinicID]
        commit('core/PATCH_SCHEDULE_SLOT', {clinicID, payload})
    },

    partial_post_listTakenSlots ({commit, rootState}, [vm, empty, payload]) {
        const clinicID = payload.id
        const workspaces = rootState.auth.actorContext.workspaces
        payload.name = workspaces[clinicID]
        commit('core/ADD_SCHEDULE_SLOT', payload)
    }
}