import Dexie from 'dexie'
const VERSION = 9
const DB_NAME = 'LaimaDB'
const app_db = new Dexie(DB_NAME)
const app_stores = {
    sync: 'table_name,hashsum',
    clinic: '&id,email,office_count,domain,phone,default_visit_interval',
    doctors: '&actor,name,last_name',
    slots: '&id,*during,patient.id,patient.patient_id,patient.full_name,status,scheduleID,date',
    schedules: '&id,date,doctor',
    visit: '&id,date,doctor.id,workspace.id,schedule_slot.id,patient.id',
    patient: '&id,patient_id,actor,name'
}
app_db.version(VERSION).stores(app_stores)

export default app_db